:root {
  --main_font: "Architype", sans-serif;
  --color_white: #fff;
  --color_black: #000;
  --color_red: #fa4132;
  --color_azure: #007d8a;
  --color_orange: #f2c75c;
}

@font-face {
  font-family: "Architype";
  src: url("../fonts/Architype-Light.woff2") format("woff2"), url("../fonts/Architype-Light.woff") format("woff"), url("../fonts/Architype-Light.ttf") format("ttf");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Architype";
  src: url("../fonts/Architype-Bold.woff2") format("woff2"), url("../fonts/Architype-Bold.woff") format("woff"), url("../fonts/Architype-Bold.ttf") format("ttf");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
.svg-image-arrow-right, .svg-image-arrow-left {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-arrow-left {
  background-position: 0 0;
  background-size: 100% auto;
  width: 34px;
}

.svg-image-arrow-left:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 41.1764705882%;
}

.svg-image-arrow-left-dims {
  width: 34px;
  height: 14px;
}

.svg-image-arrow-right {
  background-position: 0 100%;
  background-size: 100% auto;
  width: 34px;
}

.svg-image-arrow-right:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 41.1764705882%;
}

.svg-image-arrow-right-dims {
  width: 34px;
  height: 14px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

/*===== global styles =====*/
*,
::before,
::after {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  color: #000;
  font-family: var(--main_font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 991px) {
  .container {
    padding: 0 20px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  font-family: inherit;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

button {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.btn {
  width: max-content;
  display: flex;
  align-items: center;
  gap: 0 24px;
  padding: 10px 24px 8px 24px;
  border-radius: 28px;
  font-size: 14px;
  font-weight: 800;
  line-height: 32px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s ease-out;
}
.btn svg {
  transform-origin: center center;
}
.btn svg,
.btn path {
  transition: all 0.2s ease-out;
}
@media (hover: hover) {
  .btn:hover svg {
    transform: rotate(45deg);
  }
}
@media only screen and (max-width: 1199px) {
  .btn {
    padding: 10px 24px 10px 24px;
    font-size: 16px;
  }
}

.btn-red-to-white {
  background-color: var(--color_red, #fa4132);
  color: var(--color_white, #fff);
}
.btn-red-to-white path {
  fill: var(--color_white, #fff);
}
@media (hover: hover) {
  .btn-red-to-white:hover {
    background-color: var(--color_white, #fff);
    color: var(--color_red, #fa4132);
  }
  .btn-red-to-white:hover path {
    fill: var(--color_red, #fa4132);
  }
}

.btn-red-to-black {
  background-color: var(--color_red, #fa4132);
  color: var(--color_white, #fff);
}
.btn-red-to-black path {
  fill: var(--color_white, #fff);
}
@media (hover: hover) {
  .btn-red-to-black:hover {
    background-color: var(--color_black, #000);
    color: var(--color_white, #fff);
  }
  .btn-red-to-black:hover path {
    fill: var(--color_white, #fff);
  }
}

.btn-transparent-to-white {
  background-color: transparent;
  color: var(--color_white, #fff);
  border: 2px solid var(--color_white, #fff);
}
.btn-transparent-to-white path {
  fill: var(--color_white, #fff);
}
@media (hover: hover) {
  .btn-transparent-to-white:hover {
    background-color: var(--color_white, #fff);
    color: var(--color_red, #fa4132);
  }
  .btn-transparent-to-white:hover path {
    fill: var(--color_red, #fa4132);
  }
}

.btn-white-to-black {
  background-color: var(--color_white, #fff);
  color: var(--color_red, #fa4132);
  border: 2px solid var(--color_white, #fff);
}
.btn-white-to-black path {
  fill: var(--color_red, #fa4132);
}
@media (hover: hover) {
  .btn-white-to-black:hover {
    background-color: var(--color_black, #000);
    color: var(--color_white, #fff);
  }
  .btn-white-to-black:hover path {
    fill: var(--color_white, #fff);
  }
}

.block__title {
  font-size: 52px;
  font-weight: 500;
  line-height: 140%;
}
@media (max-width: 767px) {
  .block__title {
    font-size: 44px;
  }
}
@media (max-width: 575px) {
  .block__title {
    max-width: 100%;
  }
}

.readmore__link {
  position: relative;
  font-size: 16px;
  font-weight: 800;
  line-height: 32px;
  text-transform: uppercase;
}
.readmore__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 1px;
  width: 100%;
  background-color: var(--color_white, #fff);
  opacity: 0;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .readmore__link:hover::after {
    opacity: 1;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  min-height: 75px;
  background-color: #090807;
}
@media only screen and (max-width: 1199px) {
  .header {
    min-height: 72px;
  }
  .header::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 72px;
    background-color: #000;
  }
}
.header.scrolled {
  box-shadow: 0 2px 8px 4px rgba(9, 8, 7, 0.2);
}

.header__container {
  width: 100%;
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
  padding: 11px 40px 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1299px) {
  .header__container {
    max-width: 1220px;
  }
}
@media only screen and (max-width: 1199px) {
  .header__container {
    padding: 16px 20px 16px;
  }
}

.header__logo {
  width: 127px;
  height: 47px;
}
.header__logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media only screen and (max-width: 1199px) {
  .header__logo {
    position: relative;
    z-index: 10;
    width: 109px;
    height: 40px;
  }
}

.header__menu {
  display: flex;
  align-items: center;
  gap: 0 24px;
}
@media only screen and (max-width: 1199px) {
  .header__menu {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    display: block;
    padding: 116px 70px;
    background-color: #000;
    overflow-y: auto;
    transform: translateX(0);
    transition: all 0.3s ease-out;
  }
  .header__menu.active {
    transform: translateX(100%);
  }
}
@media (max-width: 767px) {
  .header__menu {
    padding: 116px 40px;
  }
}

.header__nav {
  display: flex;
  align-items: center;
}
.header__nav ul {
  display: flex;
  align-items: center;
  gap: 0 25px;
}
.header__nav li {
  color: var(--color_white, #fff);
  font-size: 14px;
  font-weight: 800;
  line-height: 32px;
  text-transform: uppercase;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header__nav li:hover {
    color: var(--color_red, #fa4132);
  }
}
@media only screen and (max-width: 1199px) {
  .header__nav {
    display: block;
    margin-bottom: 87px;
  }
  .header__nav ul {
    display: block;
  }
  .header__nav li {
    font-size: 16px;
    width: max-content;
  }
  .header__nav li:not(:last-child) {
    margin-bottom: 32px;
  }
}

@media only screen and (max-width: 1199px) {
  .header__get-in-touch {
    display: none;
  }
}

.header__become-a-member,
.header__become-a-patron,
.header__contact-us {
  display: none;
}
@media only screen and (max-width: 1199px) {
  .header__become-a-member,
.header__become-a-patron,
.header__contact-us {
    display: flex;
  }
  .header__become-a-member:not(:last-child),
.header__become-a-patron:not(:last-child),
.header__contact-us:not(:last-child) {
    margin-bottom: 40px;
  }
}

.header__burger {
  display: none;
  position: relative;
  width: 44px;
  height: 28px;
  cursor: pointer;
}
.header__burger span {
  position: absolute;
  right: 0;
  top: 12px;
  width: 32px;
  height: 4px;
  background-color: #fff;
  transition: all 0.2s ease-out;
}
.header__burger::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 4px;
  background-color: #fff;
  transition: all 0.2s ease-out;
  transform-origin: right center;
}
.header__burger::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 4px;
  background-color: #fff;
  transition: all 0.2s ease-out;
  transform-origin: right center;
}
@media (hover: hover) {
  .header__burger:hover span {
    width: 100%;
  }
  .header__burger:hover::before {
    width: 100%;
  }
}
.header__burger.active span {
  opacity: 0;
}
.header__burger.active::before, .header__burger.active::after {
  width: 100%;
}
.header__burger.active::before {
  transform: rotate(-45deg) translateY(-5px);
}
.header__burger.active::after {
  transform: rotate(45deg) translateY(5px);
}
@media only screen and (max-width: 1199px) {
  .header__burger {
    display: block;
    position: relative;
    z-index: 10;
  }
}

.main {
  flex-grow: 1;
}

.intro {
  position: relative;
  width: 100%;
  min-width: 320px;
  overflow: hidden;
}
.intro .intro__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #000;
  background: center/cover no-repeat;
  background-image: url("../images/intro/intro-bg.png");
  background-image: image-set("../images/intro/intro-bg.png" type("image/webp"), "../images/intro/intro-bg.webp" type("image/png"));
}
@media (max-width: 1299px) {
  .intro .intro__bg {
    background-image: url("../images/intro/intro-bg-1280.png");
    background-image: image-set("../images/intro/intro-bg-1280.png" type("image/webp"), "../images/intro/intro-bg-1280.webp" type("image/png"));
  }
}
@media (max-width: 575px) {
  .intro .intro__bg {
    background-image: url("../images/intro/intro-bg-400.png");
    background-image: image-set("../images/intro/intro-bg-400.png" type("image/webp"), "../images/intro/intro-bg-400.webp" type("image/png"));
  }
}

.intro__title {
  font-size: 0;
  line-height: 0;
}

.intro__container {
  width: 100%;
  max-width: 1380px;
  margin: 0 auto;
  padding: 75px 40px 0;
  display: grid;
  grid-template-columns: 1fr 1px 459px 1px 1fr;
  grid-template-areas: "inspiring line_first excellence line_second ." "about line_first . line_second scrolldown";
}
@media (max-width: 1299px) {
  .intro__container {
    grid-template-columns: 1fr 1px 405px 1px 1fr;
    padding: 75px 70px 0;
  }
}
@media only screen and (max-width: 1199px) {
  .intro__container {
    grid-template-columns: 1fr 1px 1fr 1px 1fr;
    padding: 72px 40px 0;
  }
}
@media (max-width: 991px) {
  .intro__container {
    grid-template-columns: 1fr;
    grid-template-areas: "inspiring" "excellence" "about" "scrolldown";
    padding: 72px 40px 106px;
  }
}
@media (max-width: 767px) {
  .intro__container {
    padding: 72px 20px 106px;
  }
}

.intro__inspiring {
  position: relative;
  grid-area: inspiring;
  padding: 119px 20px 74px 0;
  color: var(--color_white, #fff);
  font-size: 52px;
  font-weight: 500;
  line-height: 140%;
}
.intro__inspiring::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  transform: translateX(-50%);
  width: 200vw;
  height: 1px;
  background-color: #fff;
}
@media only screen and (max-width: 1199px) {
  .intro__inspiring {
    font-size: 48px;
    padding: 119px 15px 74px 0;
  }
}
@media (max-width: 991px) {
  .intro__inspiring {
    max-width: 360px;
    padding: 101px 15px 20px 0;
    font-size: 44px;
  }
  .intro__inspiring::after {
    background: linear-gradient(90deg, #fff 0%, rgba(255, 255, 255, 0) 100%);
  }
}

.intro__excellence {
  grid-area: excellence;
  padding: 119px 20px 74px 40px;
  color: var(--color_red, #fa4132);
  font-size: 52px;
  font-weight: 500;
  line-height: 140%;
}
@media only screen and (max-width: 1199px) {
  .intro__excellence {
    font-size: 48px;
    padding: 119px 15px 74px;
  }
}
@media (max-width: 991px) {
  .intro__excellence {
    max-width: 360px;
    padding: 22px 15px 0 0;
    font-size: 44px;
  }
}

.intro__about {
  grid-area: about;
  padding: 60px 20px 87px 0;
  color: var(--color_white, #fff);
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
}
@media only screen and (max-width: 1199px) {
  .intro__about {
    font-size: 22px;
    padding: 60px 15px 87px 0;
  }
}
@media (max-width: 991px) {
  .intro__about {
    max-width: 480px;
    padding: 19px 0;
    font-size: 20px;
    line-height: 28px;
  }
}

